const editorEvents = [
  'change',
  'focus',
  'input',
  'blur',
  'copy',
  'paste',
  'selection-change',
  'cursor-change',
  'scroll',
  'handle-options',
];

/**
 *  waitfunc 
 *
 * @param  {function} func        
 * @param  {number}   wait        
 * @param  {boolean}  immediate   ture
 * @return {function}             
 */
function debounce(func, wait = 0, immediate = true) {
  let timer;
  let context;
  let args;

  // 
  const later = () => setTimeout(() => {
    // 
    timer = null;
    // 
    // 
    if (!immediate) {
      func.apply(context, args);
      context = null;
      args = null;
    }
  }, wait);

  // 
  return function fn(...params) {
    // later
    if (!timer) {
      timer = later();
      // 
      // 
      if (immediate) {
        func.apply(this, params);
      } else {
        context = this;
        args = params;
      }
    // later
    // 
    } else {
      clearTimeout(timer);
      timer = later();
    }
  };
}
export {
  editorOptions,
  editorEvents,
  debounce,
};
